<template>
  <div>
    <el-table
      :data="applyCANRecordList"
      style="width:100%">
      <el-table-column
        label="日期"
      >
        <template slot-scope="scope">
          <p class="">{{ scope.row.createdAt | subDate }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="收幣地址"
        prop="canReceiveAddress">
      </el-table-column>
      <el-table-column
        label="金額 (CAN)">
        <template slot-scope="scope">
          <p class="inCanClass" v-if="scope.row.inOrOut === 'in'"><span>+</span>{{ scope.row.amount }}</p>
          <p class="outCanClass" v-else><span>-</span>{{ scope.row.amount }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="狀態">
        <template slot-scope="scope">
          <p class="theme-fontColor">{{ scope.row.status | applyCanStatus }}</p>
        </template>
      </el-table-column>
    </el-table>
    <!--==============/分页==============-->
    <div class="pagination-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        layout="prev, pager, next, jumper"
        :total="countsNumber"
        background
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import { UTIL } from '../../util/util'
  export default {
    props: ['watchTibiSuccess'],
    data () {
      return {
        applyCANRecordList: [],
        currentPage: 1,
        countsNumber: 1
      }
    },
    watch: {
      watchTibiSuccess (newdata) {
        if (newdata === true) {
          this.applyRecordDataRequest()
        }
      }
    },
    mounted () {
      this.applyRecordDataRequest()
    },
    methods: {
      // 提币申请记录查询接口
      applyRecordDataRequest () {
        let vm = this
        axios.get('/promo/authed/account/coins/extract/lists/' + vm.currentPage + '/10')
          .then(function (response) {
            if (Number(response.data.count) === 0) {
              vm.countsNumber = 1
            } else {
              vm.countsNumber = Number(response.data.count)
            }
            vm.applyCANRecordList = UTIL.fliterNullArr(response.data.list)
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      handleCurrentChange (val) {      // 分页加载
        document.documentElement.scrollTop = 200
        this.currentPage = val
        this.applyRecordDataRequest()
      }
    }
  }
</script>
<style scoped>
  .inCanClass {
    color:#52C41A
  }
  .outCanClass {
    color:#F5222D
  }
  .inCanClass span {
    margin-right: 4px;
  }
  .outCanClass span {
    margin-right: 4px;
  }
</style>



